

import { Button, Card, CardContent, Grid, Typography, withStyles } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import { expandStrapiImg, rupiah } from "../../util";


/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    button: {
    },
    label: {
        // Aligns the content of the button vertically.
        flexDirection: 'column',
        alignItems: 'stretch',
    },
    title: {
        textAlign: 'left',
    },
    thumb: {
        objectFit: 'cover',
        width: '100%',
        height: '25vw',
        maxHeight: '200px',
    }
})

const GoodTile = ({ item, classes }) => {
    return (
        <Grid item xs={6} sm={4} md={3} key={item.title}>
            <Button
                classes={{ root: classes.button, label: classes.label }}
                fullWidth
                onClick={item.click || (() => navigate('/pasar/barang?id='+item.id))}
            >
                <Card elevation={3}>
                    <img src={expandStrapiImg(item.galleries[0].url)} className={classes.thumb} alt="" />
                    <CardContent>
                        <Typography align="left" variant="subtitle1">
                            {item.title}
                        </Typography>
                        <Typography align="left" gutterBottom variant="subtitle2">
                            {rupiah(item.price)}
                        </Typography>
                    </CardContent>
                </Card>
            </Button>
        </Grid>
    );
}

export default withStyles(styles)(GoodTile)
