import * as React from "react";
import save from "../../components/save";
import Defer from "../../components/ui/Defer";
import { useFetcher } from '../../components/ajax';
import Navbar from "../../components/ui/Navbar";
import { AppBar, Button, ButtonBase, Card, CardContent, Grid, List, ListItem, Menu, MenuItem, Typography, withStyles } from "@material-ui/core";
import Carousel from 'react-material-ui-carousel';
import ReqIcon from '@material-ui/icons/Warning';

// @ts-ignore
import b1 from '../../images/Icon Listrik.png';
// @ts-ignore
import b2 from '../../images/Icon Pulsa.png';
// @ts-ignore
import b3 from '../../images/Icon BPJS.png';
// @ts-ignore
import b4 from '../../images/Icon PDAM.png';
// @ts-ignore
// @ts-ignore
// @ts-ignore
// @ts-ignore

// @ts-ignore
import b11 from '../../images/Icon Demografi.png';
// @ts-ignore
import b12 from '../../images/Icon Market.png';
// @ts-ignore
import b13 from '../../images/Icon Surat.png';
// @ts-ignore
import b14 from '../../images/Icon Ecomplain.png';
// @ts-ignore
import b15 from '../../images/Icon Emergency.png';
// @ts-ignore
import e1 from '../../images/Topup.png';
// @ts-ignore
import e2 from '../../images/Transfer.png';
// @ts-ignore
import e3 from '../../images/History.png';
// @ts-ignore
import e4 from '../../images/icon_saldo.png';
import { navigate } from "gatsby";
import { expandImg, rupiah } from "../../components/util";
import GoodTile from "../../components/ui/market/GoodTile";
import { primaryColor } from "../../components/style";

/**
 * @typedef {import('../../../../shared/types/userInfo').UserInfo} UserInfo
 */

const bottomIcons = [
    {
        "title": "Informasi",
        "icon": b11,
        "click": () => navigate('/desa/info/'),
    },
    {
        "title": "Surat",
        "icon": b13,
        "click": () => navigate('/desa/surat/'),
    },
    {
        "title": "Keluhan",
        "icon": b14,
        "click": () => navigate('/desa/komplain/'),
    },
    {
        "title": "Darurat",
        "icon": b15,
        "click": () => navigate('/desa/darurat/'),
    },
];

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    button: {
    },
    label: {
        // Aligns the content of the button vertically.
        flexDirection: 'column'
    },
    icon: {
        marginBottom: theme.spacing(1),
        objectFit: 'contain',
        width: '40px',
        height: '40px',
    },
    thumb: {
        marginBottom: theme.spacing(1),
        objectFit: 'cover',
        width: 'calc(25vw - 60px)',
        height: 'calc(25vw - 60px)',
    }
})


const BillerAccess = withStyles(styles)((/** @type {{user: UserInfo, classes: any}} */{ user, classes }) => {
    const [aPulsaEl, setPulsaEl] = React.useState(null);
    const [aDebitEl, setDebitEl] = React.useState(null);
    const [aPlnEl, setPlnEl] = React.useState(null);

    if (!user.features.billing) {
        return (
            <ButtonBase onClick={() => navigate('/user/pin')} style={{ width: '100%', padding: '2em' }}>
                <Card style={{ width: '100%' }}>
                    <CardContent>
                        <ReqIcon />
                        <Typography align="center" gutterBottom>
                            Masukkan PIN terlebih dahulu untuk membuka layanan pembayaran.
                        </Typography>
                        <Button variant="contained" color="primary">
                            Atur disini
                        </Button>
                    </CardContent>
                </Card>
            </ButtonBase>
        );
    }
    const billerIcons = [
        {
            "title": "Listrik",
            "icon": b1,
            "click": (event) => setPlnEl(event.currentTarget),
        },
        {
            "title": "Pulsa",
            "icon": b2,
            "click": (event) => setPulsaEl(event.currentTarget),
        },
        {
            "title": "BPJS",
            "icon": b3,
            "click": (event) => { },
        },
        {
            "title": "PDAM",
            "icon": b4,
            "click": (event) => { },
        },
    ];
    return (
        <>
            <List>
                <ListItem style={{ flexDirection: 'column' }} divider>
                    <Button size="large" style={{ margin: '1em' }} variant="outlined" onClick={(event) => setDebitEl(event.currentTarget)}>
                        <img src={e4} alt="" width="30px" style={{ marginRight: '30px' }} />
                        <Typography variant="h6" style={{ textTransform: 'initial' }}>
                            {rupiah(user.account.balance)},-
                        </Typography>
                    </Button>
                    <Grid container justifyContent="center" style={{ margin: '1em' }}>
                        {
                            billerIcons.map(x => (
                                <Grid item key={x.title} xs={3}>
                                    <Button classes={{ root: classes.button, label: classes.label }} fullWidth onClick={x.click}>
                                        <img src={x.icon} alt="" className={classes.icon} />
                                        {x.title}
                                    </Button>
                                </Grid>
                            ))
                        }
                    </Grid>
                </ListItem>
            </List>
            <Menu
                anchorEl={aPulsaEl}
                keepMounted
                open={Boolean(aPulsaEl)}
                onClose={() => setPulsaEl(null)}
            >
                <MenuItem onClick={() => navigate('/digital/pulsa/prabayar')}>
                    <img src={b2} alt="" width="20px" style={{ marginRight: '20px' }} />
                    Prabayar
                </MenuItem>
            </Menu>
            <Menu
                anchorEl={aPlnEl}
                keepMounted
                open={Boolean(aPlnEl)}
                onClose={() => setPlnEl(null)}
            >
                <MenuItem onClick={() => navigate('/digital/listrik/token')}>
                    <img src={b1} alt="" width="20px" style={{ marginRight: '20px' }} />
                    Token
                </MenuItem>
                <MenuItem onClick={() => navigate('/digital/listrik/tagihan')}>
                    <img src={b1} alt="" width="20px" style={{ marginRight: '20px' }} />
                    Tagihan
                </MenuItem>
                <MenuItem onClick={() => navigate('/digital/listrik/nontaglis')}>
                    <img src={b1} alt="" width="20px" style={{ marginRight: '20px' }} />
                    Non-Tagihan
                </MenuItem>
            </Menu>
            <Menu
                anchorEl={aDebitEl}
                keepMounted
                open={Boolean(aDebitEl)}
                onClose={() => setDebitEl(null)}
            >
                <MenuItem onClick={() => navigate('/digital/topup')}>
                    <img src={e1} alt="" style={{ marginRight: '20px' }} />
                    Topup
                </MenuItem>
                <MenuItem onClick={() => navigate('/digital/transfer')}>
                    <img src={e2} alt="" style={{ marginRight: '20px' }} />
                    Transfer
                </MenuItem>
                <MenuItem onClick={() => navigate('/digital/history')}>
                    <img src={e3} alt="" style={{ marginRight: '20px' }} />
                    History
                </MenuItem>
            </Menu>

        </>
    );
});

const PageDesaAccess = withStyles(styles)((/** @type {{user: UserInfo, classes: any}} */{ user, classes }) => {
    if (!user.account.geocode) {
        return (
            <ButtonBase onClick={() => navigate('/user/geo')} style={{ width: '100%', padding: '2em' }}>
                <Card style={{ width: '100%' }}>
                    <CardContent>
                        <ReqIcon />
                        <Typography align="center" gutterBottom>
                            Pilih Lokasi Desa terlebih dahulu untuk membuka layanan desa.
                        </Typography>
                        <Button variant="contained" color="primary">
                            Atur disini
                        </Button>
                    </CardContent>
                </Card>
            </ButtonBase>
        );
    }
    const listingProd = useFetcher('/market/discover/goods?sort=newest');
    return (
        <>
            {user.features.billing && <List>
                <ListItem style={{ flexDirection: 'column' }} divider>
                    <Typography gutterBottom align="center">
                        Belanja di Pasar Online Area <b>{user.geolookup.kecamatan}</b>
                    </Typography>
                </ListItem>
                <ListItem style={{ flexDirection: 'column' }} divider>
                    <Defer data={listingProd}>
                        {() => <>
                            <Grid container spacing={2} justifyContent="center">
                                {
                                    listingProd.data.map(x => (
                                        <GoodTile item={x} key={x.id} />
                                    ))
                                }
                            </Grid>
                        </>}
                    </Defer>
                </ListItem>
                <ListItem>
                    <Button fullWidth variant="outlined" onClick={() => navigate('/pasar/discover')}
                        startIcon={<img src={b12} className={classes.icon} alt="" />}
                        style={{ margin: '1em 0' }}>
                        Cari lebih banyak produk
                    </Button>
                </ListItem>
            </List>}
            <div style={{ height: '100px' }} />
            <AppBar position="fixed" elevation={5} style={{ bottom: 0, top: 'auto' }}>
                <Grid container justifyContent="center">
                    {
                        bottomIcons.map(x => (
                            <Grid item xs={3} key={x.title}>
                                <Button
                                    classes={{ root: classes.button, label: classes.label }} fullWidth
                                    onClick={x.click}
                                >
                                    <img src={x.icon} className={classes.icon} alt="" />
                                    {x.title}
                                </Button>
                            </Grid>
                        ))
                    }
                </Grid>
            </AppBar>
        </>
    );
});

const Page = () => {
    /**
     * @type {UserInfo}
     */
    const data = useFetcher('/auth/info', (d) => {
        save.userInfo = d;
    });
    return (
        <Defer data={data}>
            {() => (
                <>
                    <div style={{ background: primaryColor }}>
                        <Navbar />
                        <List>
                            <ListItem style={{ flexDirection: 'column' }}>
                                <Carousel>
                                    {
                                        data.promo.central.map(x => (
                                            <div key={x} style={{ textAlign: 'center' }}>
                                                <img src={expandImg(x)} alt='' height="200px" />
                                            </div>
                                        ))
                                    }
                                </Carousel>
                            </ListItem>
                        </List>
                    </div>
                    <div>
                        <BillerAccess user={data} />
                        <PageDesaAccess user={data} />
                    </div>
                </>
            )}
        </Defer>
    );
}

export default Page;
